//   }
// }

header {
  position: relative;
  margin: 33px auto 56px;
  max-width: 80%;
  .logo {
    display: inline-block;
    vertical-align: bottom;
  }
  .logo, .franklabs a {
    text-decoration: none;
  }
  .franklabs {
    font-size: 2.1em;
    &:hover {
      text-decoration: underline;
    }
  }

  @media screen and (min-width: 600px) {
    max-width: 456px;
    margin: 44px auto 68px;
    .franklabs {
      font-size: 2.5em;
    }
  }
  @media screen and (min-width: 1200px) {
    max-width: 550px;
    // margin: 67px auto 108px;
    .franklabs {
      font-size: 2.7em;
    }
  }
  @media screen and (min-width: 1600px) {
    max-width: 570px;
    margin: 67px auto 108px;
    .franklabs {
      font-size: 2.9em;
    }
  }
  img {
    width: 100%;
    display: inline-block;
    vertical-align: bottom;
    @media screen and (min-width: 600px) {
      width: 170px;
      margin-bottom: -4px;
    }
    @media screen and (min-width: 1200px) {
      width: 245px;
      margin-bottom: -7px;
    }
    @media screen and (min-width: 1600px) {
      width: 250px;
      margin-bottom: -3px;
    }
  }
  .franklabs {
    display: inline-block;
    margin-left: 0.4em;
  }
  i {
    color: $theme-color-base;
  }
  .menu {
    text-align: center;
    a {
      color: $transparent-heavy;
      @media screen and (prefers-color-scheme: dark) {
        color: $dm-transparent-heavy;
      }
    }
    ul {
      li {
        display: inline-block;
        margin-right: .6em;
      }
    }
    @media screen and (min-width: 600px) {
      text-align: left;
      position: absolute;
      right: -14px;
      bottom: -10px;
    }
    @media screen and (min-width: 1200px) {

      bottom: -8px;
    }
    @media screen and (min-width: 1600px) {

      bottom: -6px;
    }
  }
}



.container {
  position: relative;
  background-color: $theme-color-base;
  box-shadow:
    -1.9px 0 1.2px -6px rgba(0, 0, 0, 0.072),
    -4.3px 0 2.7px -6px rgba(0, 0, 0, 0.105),
    -7.7px 0 4.8px -6px rgba(0, 0, 0, 0.129),
    -12.8px 0 8px -6px rgba(0, 0, 0, 0.151),
    -21.2px 0 13.2px -6px rgba(0, 0, 0, 0.175),
    -37px 0 23.1px -6px rgba(0, 0, 0, 0.208),
    -80px 0 50px -6px rgba(0, 0, 0, 0.28),
    15px 0 20px -6px rgb(158, 204, 201)
  ;
  border-radius: 24px;
  border: 1px solid rgba(255,255,255,0.7);
  @media screen and (prefers-color-scheme: dark) {
    background-color: $dm-theme-color-base;
    border: 1px solid rgba(0,0,0,0.4);
    box-shadow:
      -1.9px 0 1.2px -6px rgba(0, 0, 0, 0.072),
      -4.3px 0 2.7px -6px rgba(0, 0, 0, 0.105),
      -7.7px 0 4.8px -6px rgba(0, 0, 0, 0.129),
      -12.8px 0 8px -6px rgba(0, 0, 0, 0.151),
      -21.2px 0 13.2px -6px rgba(0, 0, 0, 0.175),
      -37px 0 23.1px -6px rgba(0, 0, 0, 0.208),
      -80px 0 50px -6px rgba(0, 0, 0, 0.28),
      15px 0 20px -6px #5c7775
    ;
  }



  h2 {
    i {
      // the labs in franklabs
      font-weight: 600;
      letter-spacing: -3.8px;
    }
  }

  .metadata {
    position: relative;
    ul {
      margin-bottom: 2em;
      li {
        font-style: italic;
        color: $transparent-medium;
        @media screen and (prefers-color-scheme: dark) {
          color: $dm-transparent-heavy;
        }
        margin-left: 49px;
      }
    }
    .avatar {
      float: right;
      position: absolute;
      top: -2px;
      left: 0px;
      width: 36px;
      @media screen and (min-width: 1200px) {
        float: none;
        position: relative;
        top: 0px;
        width: 104px;
        margin-top: 10px;
      }
    }
    @media screen and (min-width: 1200px) {
      position: absolute;
      right: -267px;
      width: 250px;
      ul {
        li {
          margin-left: 0;
        }
      }
    }
  }

  .spacer-ava-gr1200 {
    @media screen and (min-width: 1200px) {
      height: 60px;
      width: 100%;
    }
  }

  figure {
    img {
      width: 100%;
      box-shadow: -69px 0px 54px -10px rgba(20, 36, 47, 0.6);
    }
    figcaption {
      width: 100%;
      margin: 1em 0;
      text-align: center;
      @media screen and (prefers-color-scheme: dark) {
        color: $dm-black;
      }
    }
    width: 120%;
    margin: 1.1em 0px 1.2em -10%;
    @media screen and (min-width: 600px) {
      width: 128%;
      margin: 1.5em 0px 1.1em -14%;
    }
    @media screen and (min-width: 1200px) {
      width: 140%;
      margin: 2em 0px 1.8em -20%;
    }
    @media screen and (min-width: 1600px) {
      width: 152%;
      margin: 2.6em 0px 2.4em -26%;
    }
  }

  .intro {
    border-bottom: 1px solid $theme-color-accent-light;
    @media screen and (prefers-color-scheme: dark) {
      border-bottom: 1px solid $dm-theme-color-accent-light;
    }
    padding-bottom: 1em;
  }

  .intermezzo {
    border: 1px solid black;
    @media screen and (prefers-color-scheme: dark) {
      border: 1px solid $dm-black;
    }
    padding: 1em 5% 0.4em;
    margin: 1.3em 0 1.1em;
    @media screen and (min-width: 600px) {
      border: 1px solid black;
      @media screen and (prefers-color-scheme: dark) {
        border: 1px solid $dm-black;
      }
    }
    @media screen and (min-width: 1200px) {
      border: 1px solid black;
      @media screen and (prefers-color-scheme: dark) {
        border: 1px solid $dm-black;
      }
      margin: 1.4em 0 1.2em;
    }
    @media screen and (min-width: 1600px) {
      position: absolute;
      right: -379px;
      width: 250px;
      margin-top: 0px;
      padding: 1.4em 5% 1.0em;
    }
    h4 {
      margin-top: 0px;
    }
  }

  .button {
    background-color: $transparent-light;
    @media screen and (prefers-color-scheme: dark) {
      background-color: $dm-transparent-light;
    }
    border: 1px solid rgba(0, 0, 0, 0);
    color: $white;
    @media screen and (prefers-color-scheme: dark) {
      color: $white;
    }
    padding: 1px 13px 4px;
    text-decoration: none;
    -webkit-font-smoothing: antialiased;
    border-radius: 22px;
    &:hover {
      border: 1px solid $black;
      @media screen and (prefers-color-scheme: dark) {
        border: 1px solid $dm-black;
      }
      background-color: transparent;
      color: $black;
      @media screen and (prefers-color-scheme: dark) {
        color: $dm-transparent-heavy;
      }
      cursor: pointer;
    }
    &.inverse {
      background-color: $white;
      @media screen and (prefers-color-scheme: dark) {
        background-color: $dm-white;
      }
      border: 1px solid $black;
      @media screen and (prefers-color-scheme: dark) {
        border: 1px solid $dm-black;
      }
      color: $black;
      @media screen and (prefers-color-scheme: dark) {
        color: $dm-black;
      }
      &:hover {
        background-color: $transparent-light;
        @media screen and (prefers-color-scheme: dark) {
          background-color: $dm-transparent-light;
        }
        border: 1px solid rgba(0, 0, 0, 0);
        color: $white;
        @media screen and (prefers-color-scheme: dark) {
          color: $dm-white;
        }
      }
    }
  }

  .button-large {
    display: inline-block;
    padding: 10px 12px;
    margin-right: 6px;
    border-radius: 8px;
    text-decoration: none;
    background: linear-gradient(90deg, #cccccc, #d9d9d9);
    box-shadow: -6px 0px 12px -4px #969696, 2px 0px 13px #fff;
    @media screen and (prefers-color-scheme: dark) {
      background: linear-gradient(90deg, #4d4d4d, #808080);
      color: $dm-black;
      box-shadow: -9px 0px 12px -4px #0d0d0d, 4px 0px 9px -3px #5f5f5f;
    }
    &:hover {
      background: linear-gradient(204deg, #bdc2c2, #898d8d);
      @media screen and (prefers-color-scheme: dark) {
        background: linear-gradient(204deg, #c2c2c2, #8c8c8c);
        color: $black;
      }
    }
    xbackground: linear-gradient(204deg, #dadfdf, #a5a8a8);

  }

  .image-homepage,
  .image-over {
    object-fit: cover;
    border-radius: 8px;
  }

  .image-homepage {
    height: 260px;
    @media only screen and (min-width: $breakpoint-small) {
      height: 360px;
    }
  }

  .gradient-text {
    display: inline-block;
    background: -webkit-linear-gradient(left,#2c424d, #557880);
    background: linear-gradient(to right,#2c424d, #557880);
        background-clip: border-box;
    color: rgba(233,70,150,0);
    -webkit-background-clip: text;
    @media screen and (prefers-color-scheme: dark) {
      background: -webkit-linear-gradient(left,#627e7b, #77938b);
      background: linear-gradient(to right,#627e7b, #77938b);
          background-clip: border-box;
      color: rgba(233,70,150,0);
      -webkit-background-clip: text;
    }
  }

}
